
.z-carrousel-wrapper{overflow:hidden
}
.z-carrousel-wrapper ul{white-space:nowrap;font-size:0
}
.z-carrousel-wrapper ul li{list-style:none;display:inline-block
}
.z-carrousel-wrapper ul.is-transition{transition:all 300ms ease-out;-webkit-transition:all 300ms ease-out;-moz-transition:all 300ms ease-out;-o-transition:all 300ms ease-out
}
.z-carrousel-wrapper ul.dots{position:relative;bottom:25px;text-align:center
}
.z-carrousel-wrapper ul.dots li.dot{width:20px;height:20px;line-height:20px;border-radius:100%;margin:0 5px;background-color:#ccc;cursor:pointer;color:#fff;font-size:12px;text-align:center
}
.z-carrousel-wrapper ul.dots li.is-current{background-color:#41b883
}


